@import "./media-queries.scss";
.aneesWork {
  padding: 70px 0;
  background-color: #000;
  @include lt-lg {
    padding: 20px 0;
  }
  @include lt-md {
    padding: 5px 0;
  }
}
.container {
  max-width: 1200px;
  width: 100%;
  margin: auto;
}
ul.logoList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  @include lt-lg {
    padding: 0;
  }
}
@keyframes myAnim1 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes myAnim2 {
  0% {
    max-height: 0;
    max-width: 0;
    opacity: 0;
  }

  100% {
    max-height: 100px;
    max-width: 220px;
    opacity: 1;
  }
}

ul.logoList li {
  list-style: none;
  padding: 40px 46px;
  display: flex;
  align-items: center;
  border: 1px solid rgb(204 204 204 / 25%);
  margin: 14px;
  background-color: #fff;
  animation: myAnim1 0.4s ease 0s 1 normal forwards;
  opacity: 0;
  min-width: 355px;
  align-items: center;
  justify-content: center;
  @include lt-lg {
    width: calc(50% - 10px);
    margin: 5px;
    padding: 15px;
    min-height: 150px;
    min-width: auto;
  }
  @include lt-sm {
    margin: 1px;
  }
  &:hover {
    opacity: 1;
  }
}
ul.logoList .logoItem {
  transition: all ease 0.4s;
  transform: scale3d(1, 1, 1);
  &:hover {
    transform: scale3d(1.05, 1.05, 1.05);
  }
}
ul.logoList .logoItem img {
  max-height: 100px;
  max-width: 220px;
  filter: grayscale(100%);
  transition: all ease 0.1s;
  animation: myAnim2 0.1s ease 0s 1 normal forwards;
  &:hover {
    filter: grayscale(0%);
  }
  @include lt-lg {
    max-width: 100%;
    width: 100%;
    height: auto;
    padding: 0;
  }
}
.talePara {
  color: #ccc;
  text-align: center;
  padding: 0 15px;
  a {
    color: #ccc;
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
}
